import { Typography, Box } from "@mui/material";
import MyIcon from "./ico/tri.png"; // Importez votre icne ici

function Main(){
    return(
        <Box sx={{ display: 'flex', alignItems: 'center' }}>
            <img src={MyIcon} alt="Icon" style={{ width: '35px', height: '35px', marginRight: '10px' }} />
            <Typography variant="h3" component="div">
                Tri
            </Typography>
        </Box>
    )
}

export default Main;
