import { Chip, Menu, MenuItem } from "@mui/material";
import React, {useState} from 'react';

function Main(props){
    const [anchorEl, setAnchorEl] = useState(null);
    const { setPage, setLien, setAjout } = props;

    const handleClickmenu = (event) => {
        setAnchorEl(event.currentTarget);
    };

    const handleClose = () => {
        setAnchorEl(null);
    };

    const handleClick = (selectedOption) => {
        let url = "/tri/categorie"
        //+ selectedOption;
        if(window.location.hostname === "localhost" || window.location.port === "3000"){
            url = 'http://localhost:3000/tri/categorie'
            // + selectedOption;
        }
        // window.location.href = url;
        setLien(url);
        setAjout(selectedOption);
        setPage(4);
    };

    return(
        <>
            <Chip label="Catgorie" variant="outlined" onClick={handleClickmenu} style={{ backgroundColor: 'white', color: "black"}}/>
            <Menu anchorEl={anchorEl} open={Boolean(anchorEl)} onClose={handleClose} anchorOrigin={{vertical: 'bottom',horizontal: 'right',}} transformOrigin={{vertical: 'top',horizontal: 'left',}}>
                <MenuItem onClick={() => handleClick("Militaires")}>Militaires - Patrouilles</MenuItem>
                <MenuItem onClick={() => handleClick("Essai")}>Essai</MenuItem>
                <MenuItem onClick={() => handleClick("EPT")}>EPT</MenuItem>
                <MenuItem onClick={() => handleClick("Photo")}>Photo - Courses</MenuItem>
                <MenuItem onClick={() => handleClick("Parachutisme")}>Parachutisme</MenuItem>
                <MenuItem onClick={() => handleClick("Laser")}>Laser</MenuItem>
                <MenuItem onClick={() => handleClick("Evenementiel")}>Evenementiel</MenuItem>
                <MenuItem onClick={() => handleClick("Vols Sp")}>Vols Spciaux</MenuItem>
                <MenuItem onClick={() => handleClick("Divers")}>Divers</MenuItem>
                <MenuItem onClick={() => handleClick("CDS")}>Non CDS</MenuItem>
            </Menu>
        </>
    )
}

export default Main;