import Titre from "./Titre.js";
import { Box, Stack } from '@mui/material';
import Epeire from "./Epeire.js";
import Date from "./Date.js";
import Categorie from "./Categorie.js";
import Etat from "./Etat.js";
import Nom from "./Nom.js";

function Main(props) {
  const { setPage, setLien, setAjout} = props;

  return (
    <Box sx={{ display: 'flex', flexDirection: 'column', justifyContent: 'center', alignItems: 'center' }}>
        <Stack direction="column" spacing={2} justifyContent="center" alignItems="center">
          <Titre />
          <Categorie setPage={setPage} setLien={setLien} setAjout={setAjout}/>
          <Etat setPage={setPage} setLien={setLien} setAjout={setAjout}/>
          <Epeire setPage={setPage} setLien={setLien} setAjout={setAjout}/>
          <Date setPage={setPage} setLien={setLien} setAjout={setAjout}/>
          <Nom setPage={setPage} setLien={setLien} setAjout={setAjout}/>
        </Stack>
    </Box>
  )
}

export default Main;
