import React from 'react';
import { AppBar, Box, IconButton, Toolbar, Typography } from '@mui/material';
import HouseIcon from '@mui/icons-material/Home';
import Autocomplete from '@mui/material/Autocomplete';
import TextField from '@mui/material/TextField';
import { format } from 'date-fns';

function Main(props){
    const { titre, dates, setSelectedDate, setModif, modif } = props;
    const category = ["Militaires", "Patrouilles", "Essai", "EPT", "Photo", "Courses",
    "Laser", "Parachutisme", "Evenementiel", "Vols Spciaux", "Divers", "Non CDS"];
    const Epeires = ["Non Envoy", "Envoy", "Envoy mais modif"];
    const Etat = ["En attente", "En cours", "Termine", "Archive"];
    let selectedDate = new Date();

    const handleDateChange = (event) => {
        const value = event.target.textContent;
        setSelectedDate(value);
    };

    const handleModifChange = (event) => {
        const value = event.target.textContent;
        if(titre === "peire"){
            if(value === "Non Envoy"){
                setModif("white");
            }else if(value === "Envoy"){
                setModif("green");
            }else{
                setModif("yellow");
            }
        }else if(titre === "tat"){
            if(value === "En attente"){
                setModif("1");
            }else if(value === "En cours"){
                setModif("2");
            }else if(value === "Termine"){
                setModif("3");
            }else{
                setModif("4");
            }
        }else if(titre === "Nom"){
            setModif(event.target.value);
        }else if(titre === "Date"){
            setModif(event.target.value);
            selectedDate = event.target.value;
        }
        else{
            setModif(value);
        }
    };

    let tab = [];

    switch(titre){
        case "Catgorie":
            tab.push(
                <Autocomplete
                    disablePortal
                    id="Catgorie"
                    options={category}
                    getOptionLabel={(option) => option}
                    sx={{ width: 150}}
                    onChange={handleModifChange}
                    renderInput={(params) => <TextField {...params} label="Catgorie" />}
                />
            )
            break;
        case "peire":
            tab.push(
                <Autocomplete
                    disablePortal
                    id="peire"
                    options={Epeires}
                    getOptionLabel={(option) => option}
                    sx={{ width: 150}}
                    onChange={handleModifChange}
                    renderInput={(params) => <TextField {...params} label="peire" />}
                />
            )
            break;
        case "tat":
            tab.push(
                <Autocomplete
                    disablePortal
                    id="tat"
                    options={Etat}
                    getOptionLabel={(option) => option}
                    sx={{ width: 150}}
                    onChange={handleModifChange}
                    renderInput={(params) => <TextField {...params} label="tat" />}
                />
            )
            break;
        case "Date":
            tab.push(
                <input
                id="date"
                type="date"
                style={{alignItems: "center",marginTop:5}}
                value={modif instanceof Date ? format(modif, "yyyy-MM-dd") : modif}
                onChange={handleModifChange}
                />            
            )
            break;            
        case "Nom":
            tab.push(
                <TextField
                    id="Nom"
                    label="Nom"
                    sx={{ width: 150}}
                    onChange={handleModifChange}
                />    
            )
            break;
        default:
            break;
    }

    return(
        <Box sx={{ flexGrow: 1 }}>
            <AppBar position="absolute">
                <Toolbar>
                    <IconButton
                        size="large"
                        edge="start"
                        color="inherit"
                        aria-label="menu"
                        sx={{ mr: 2 }}
                        onClick={() => { window.location.href = "/"; }}
                    >
                        <HouseIcon />
                    </IconButton>
                    <Typography variant="h4" component="div" sx={{ flexGrow: 1 }}>
                        Tri {titre}
                    </Typography>
                    {tab}
                    <Autocomplete
                        disablePortal
                        id="Anne"
                        options={dates}
                        getOptionLabel={(option) => option}
                        sx={{ width: 150}}
                        onChange={handleDateChange}
                        renderInput={(params) => <TextField {...params} label="Anne" />}
                    />
                </Toolbar>
            </AppBar>
        </Box>
    )
}

export default Main;
