import { Chip, Menu, MenuItem, TextField } from "@mui/material";
import React, {useState} from 'react';

function Main(props){
    const [anchorEl, setAnchorEl] = useState(null);
    const [inputValue, setInputValue] = useState("");
    const { setPage, setLien, setAjout } = props;

    const handleClickmenu = (event) => {
        setAnchorEl(event.currentTarget);
    };

    const handleClose = () => {
        setAnchorEl(null);
        setInputValue("");
    };

    const handleClick = () => {
        let url = "/tri/nom"
        //+inputValue;
        if(window.location.hostname === "localhost" || window.location.port === "3000"){
            url = 'http://localhost:3000/tri/nom'
            //+inputValue;
        }
        // window.location.href = url;
        setLien(url);
        setAjout(inputValue);
        setPage(8);
    };

    const handleChange = (event) => {
        setInputValue(event.target.value);
    };

    return(
        <>
            <Chip label="Nom" variant="outlined" onClick={handleClickmenu}/>
            <Menu anchorEl={anchorEl} open={Boolean(anchorEl)} onClose={handleClose} anchorOrigin={{vertical: 'bottom',horizontal: 'right',}} transformOrigin={{vertical: 'top',horizontal: 'left',}}>
            <MenuItem>
                    <TextField
                        label="Entrez le nom"
                        variant="outlined"
                        value={inputValue}
                        onChange={handleChange}
                    />
                </MenuItem>
                <MenuItem onClick={handleClick} sx={{textAlign: "center",justifyContent: "center",display:"flex"}}>Appliquer</MenuItem>
            </Menu>
        </>
    )
}

export default Main;