import { Chip, Menu, MenuItem } from "@mui/material";
import React, {useState} from 'react';

function Main(props){
    const [anchorEl, setAnchorEl] = useState(null);
    const { setPage, setLien, setAjout } = props;

    const handleClickmenu = (event) => {
        setAnchorEl(event.currentTarget);
    };

    const handleClose = () => {
        setAnchorEl(null);
    };
    
    const handleClick = (selectedOption) => {
        let url = "/tri/etat"
        //+selectedOption;
        if(window.location.hostname === "localhost" || window.location.port === "3000"){
            url = 'http://localhost:3000/tri/etat';
            //+selectedOption
        }
        // window.location.href = url;
        setLien(url);
        setAjout(selectedOption);
        setPage(6);
    };

    return(
        <>
            <Chip label="Etat" variant="outlined" onClick={handleClickmenu}/>
            <Menu anchorEl={anchorEl} open={Boolean(anchorEl)} onClose={handleClose} anchorOrigin={{vertical: 'bottom',horizontal: 'right',}} transformOrigin={{vertical: 'top',horizontal: 'left',}}>
                <MenuItem onClick={() => handleClick("1")}>En attente</MenuItem>
                <MenuItem onClick={() => handleClick("2")}>En cours</MenuItem>
                <MenuItem onClick={() => handleClick("3")}>Termine</MenuItem>
                <MenuItem onClick={() => handleClick("4")}>Archive</MenuItem>
            </Menu>
        </>
    )
}

export default Main;