import React, { useEffect, useState } from 'react';
import Pie from './Piechart.js';
import Navbar from './Navbar.js'

function Main(props) {
    const { lien, setPage, page } = props;
    let dates = ["2023", "2024", "2025", "2026"];
    const [donnees, setDonnees] = useState([]);
    // const [trueLien, setTrueLien] = useState(lien);
    const [selectedDate, setSelectedDate] = useState(null);
    let correspondance = ["Catgorie", "peire", "tat"];
    let titre = correspondance[page-1];

    useEffect(() => {
        const fetchData = async () => {
            try {
                let jsonData;
                let url;
                if (selectedDate === null) {
                    url = lien;
                } else {
                    url = `${lien}/${selectedDate}`;
                }
    
                const response = await fetch(url);
                jsonData = await response.json();
    
                const formattedData = Object.entries(jsonData).map(([key, value]) => ({
                    label: key,
                    value: value.count,
                    // percent: parseFloat(value.percentage).toFixed(2),
                }));
                setDonnees(formattedData);
            } catch (error) {
                console.error("Erreur lors de la rcupration des donnes:", error);
            }
        };
    
        fetchData();
    }, [lien, setPage, selectedDate]);

    return (
        <div style={{ display: "flex", flexDirection: "column", alignItems: "center" }}>
            <Navbar titre={titre} dates={dates} selectedDate={selectedDate} setSelectedDate={setSelectedDate}/>
            <div style={{ marginTop: "100px", alignItems: "center", }}>
                {donnees.length === 0 ? (
                    <p>Aucune statistique disponible.</p>
                ) : (
                    <Pie donnees={donnees} />
                )}
            </div>
        </div>
    );
    
}

export default Main;
