import React from "react";
import { DataGrid } from '@mui/x-data-grid';
import { Box } from '@mui/material';

const columns = [
    { field: 'id', headerName: 'ID',sortable: true, width: 90 },
    {field: 'name', headerName: 'Nom', sortable: true, width: 200},
    {field: 'category', headerName: 'Catgorie', width: 200},    
    {field: 'date', headerName: 'Cration', sortable: true, width: 150},
    {field: 'nbrDoc', headerName: 'Doc', width: 100,},
    {field: 'nbrPeriode', headerName: 'Periode',width: 100},
    {field: 'spider', headerName: 'Epeires', sortable: true, width: 100},
    {field: 'niveau', headerName: 'Etat', sortable: false, width: 100,},
];

function Main(props){
    const {donnees} = props;

    return(
        <div style={{ height: 400, width: '100%' }}>
            <Box sx={{ height: 800, width: '100%' }}>
                <DataGrid
                    rows={donnees}
                    columns={columns}
                    initialState={{
                    pagination: {
                        paginationModel: {
                        pageSize: 5,
                        },
                    },
                    }}
                    pageSizeOptions={[5]}
                    checkboxSelection
                    disableRowSelectionOnClick
                />
            </Box>
        </div>
    );
}

export default Main;