import { Chip } from "@mui/material";

function Main(props){
    const { setPage, setLien } = props;
    
    const handleClick = () => {
        let url = "/stat/categorie";
        if(window.location.hostname === "localhost" || window.location.port === "3000"){
            url = 'http://localhost:3000/stat/categorie';
        }
        // window.location.href = url;
        setLien(url);
        setPage(1);
    };

    return(
        <Chip label="Catgorie" variant="outlined" onClick={handleClick} >
            Categorie
        </Chip>
    )
}

export default Main;