import { Box, Stack } from '@mui/material';
import GlobalS from "./component/Menu/Stats/Global.js";
import GlobalT from "./component/Menu/Tri/Global.js";
import './App.css';
import React from 'react';
import Stat from './component/Stat/Stat.js';
import Tri from './component/Tri/Tri.js';

function App() {
  const [page, setPage] = React.useState(0); 
  const [lien, setLien] = React.useState('');
  const [Ajout, setAjout] = React.useState('');

  const tab = [];

  switch(page){
    case 0: // Menu
      tab.push(
        <div className="AppContainer">
          <Stack sx={{ justifyContent: 'center', alignItems: 'center', height: '100vh', margin: '0px' }}>
            <Box sx={{ border: '1px solid black', borderRadius: '5px', boxShadow: '5px 5px 5px rgba(0, 0, 0, 0.5)', padding: '25px', backgroundColor: 'white', width: '35vh', display: 'flex', alignItems: 'center', justifyContent: 'center', mb: 2 }}>
              <GlobalS setPage={setPage} setLien={setLien}/>
            </Box>
            <Box sx={{ border: '1px solid black', borderRadius: '5px', boxShadow: '5px 5px 5px rgba(0, 0, 0, 0.5)', padding: '25px', backgroundColor: 'white', width: '35vh', display: 'flex', alignItems: 'center', justifyContent: 'center', mb: 2 }}>
              <GlobalT setPage={setPage} setLien={setLien} setAjout={setAjout}/>
            </Box>
          </Stack>
        </div>
      );
      break;
      case 1: // Stat/Categorie
      case 2: // Stat/Epeire
      case 3: // Stat/Etat
        tab.push(
          <div className="AppContainer">
            <Stat lien={lien} setPage={setPage} page={page}/>
          </div>
        );
        break;
    case 4: // Tri/Categorie
    case 5: // Tri/Epeire
    case 6: // Tri/Etat
    case 7: // Tri/Date
    case 8: // Tri/Nom
      tab.push(
        <div className="AppContainer">
          <Tri lien={lien} setPage={setPage} page={page} Ajout={Ajout}/>
        </div>
      );
        break;
    default:
        break;
  }


  return (
    <div className="AppContainer">
      {tab}
    </div>
  );
}

export default App;
