import { PieChart, pieArcLabelClasses } from '@mui/x-charts/PieChart';
import React from 'react';

function Main(props) {
    const { donnees } = props;

    const size = {
        width: 1000,
        height: 500,
    };

    // const generateRandomColorScheme = (numColors) => {
    //     const colors = [];
    //     for (let i = 0; i < numColors; i++) {
    //         const color = `#${Math.floor(Math.random() * 16777215).toString(16)}`; // Gnrer une couleur hexadcimale alatoire
    //         colors.push(color);
    //     }
    //     return colors;
    // };

    // const colorScheme = generateRandomColorScheme(Object.keys(donnees).length);

    return (
        <PieChart
            series={[
                {
                    arcLabel: (item) => `${item.label} ${item.value}`,
                    arcLabelMinAngle: 100,
                    data: donnees,
                },
            ]}
            sx={{
                [`& .${pieArcLabelClasses.root}`]: {
                    fill: 'white',
                    fontWeight: 'bold',
                },
            }}
            // colors={colorScheme}
            {...size}
        />
    );
}

export default Main;
