import { Chip } from "@mui/material";

function Main(props){
    const { setPage, setLien } = props;
    
    const handleClick = () => {
        let url = "/stat/epeires";
        if(window.location.hostname === "localhost" || window.location.port === "3000"){
            url = 'http://localhost:3000/stat/epeires';
        }
        // window.location.href = url;
        setLien(url);
        setPage(2);
    };

    return(
        <Chip label="Epeires" variant="outlined" onClick={handleClick}/>
    )
}

export default Main;