import React, { useEffect, useState } from 'react';
import Navbar from './Navbar.js'
import Datagrid from './Datagrid.js'

function Main(props) {
    const { lien, setPage, page, Ajout } = props;
    let dates = ["2023", "2024", "2025", "2026"];
    const [donnees, setDonnees] = useState([]);
    const [modif, setModif] = useState(null);
    // const [trueLien, setTrueLien] = useState(lien);
    const [selectedDate, setSelectedDate] = useState(null);
    let correspondance = ["Catgorie", "peire", "tat", "Date", "Nom"];
    let titre = correspondance[page-4];

    useEffect(() => { // a changer le systeme pour chaque version
        const fetchData = async () => {
            try {
                let jsonData;
                let url;
                if (selectedDate === null && modif === null) { //premier affichage
                    url = lien+"/"+Ajout;
                } else {
                    if(modif === null){
                        url = `${lien}/${Ajout}/${selectedDate}`; //changement d'anne mais pas de l'objet
                    }else{
                        if(selectedDate === null){
                            url = `${lien}/${modif}`; //changement d'objet mais pas de l'anne
                        }
                        else{
                            url = `${lien}/${modif}/${selectedDate}`; //changement d'objet et de l'anne
                        }
                    }
                }
    
                const response = await fetch(url);
                jsonData = await response.json();
                setDonnees(jsonData);
            } catch (error) {
                console.error("Erreur lors de la rcupration des donnes:", error);
            }
        };
    
        fetchData();
    }, [lien, setPage, selectedDate, modif, Ajout]);

    return (
        <div style={{ display: "flex", flexDirection: "column", alignItems: "center" }}>
            <Navbar titre={titre} dates={dates} selectedDate={selectedDate} setSelectedDate={setSelectedDate} modif={modif} setModif={setModif}/>
            <div style={{ marginTop: "100px", alignItems: "center", }}>
                <Datagrid donnees={donnees} />
            </div>
        </div>
    );
    
}

export default Main;
